import React, { useState } from 'react'
import { Container, Row, Col, Form, Button, Card, Alert } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import AuthService from '../services/auth.service'

const RegisterPage = () => {
  const navigate = useNavigate()
  const [values, setValues] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    password: '',
    confirmPassword: '',
    country: '',
    city: '',
    address: '',
    gender: '',
  })
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')
  const [successful, setSuccessful] = useState(false)

  const handleChange = (e) => {
    const { name, value } = e.target
    setValues({
      ...values,
      [name]: value,
    })
  }

  const validateForm = () => {
    // Validazione della password
    if (values.password !== values.confirmPassword) {
      setMessage('Le password non corrispondono.')
      return false
    }

    if (values.password.length < 6) {
      setMessage('La password deve contenere almeno 6 caratteri.')
      return false
    }

    // Controlla che tutti i campi necessari siano compilati
    const requiredFields = [
      'firstName',
      'lastName',
      'email',
      'phone',
      'password',
      'country',
      'city',
      'address',
      'gender',
    ]
    for (const field of requiredFields) {
      if (!values[field]) {
        setMessage(`Il campo ${field}  obbligatorio.`)
        return false
      }
    }

    return true
  }

  const handleRegister = (e) => {
    e.preventDefault()
    setMessage('')
    setSuccessful(false)
    setLoading(true)

    if (!validateForm()) {
      setLoading(false)
      return
    }

    console.log('Tentativo di registrazione con:', {
      email: values.email,
      firstName: values.firstName,
      lastName: values.lastName,
    })

    console.log('URL della richiesta:', '/api/auth/signup')

    // Aggiungi un gestore per evidenziare eventuali errori CORS o di rete
    const originalFetch = window.fetch
    window.fetch = function (...args) {
      console.log('Fetch request:', args)
      return originalFetch.apply(this, args).catch((err) => {
        console.error('Fetch error:', err)
        throw err
      })
    }

    AuthService.register(
      values.firstName,
      values.lastName,
      values.email,
      values.phone,
      values.password,
      values.country,
      values.city,
      values.address,
      values.gender
    )
      .then(
        (response) => {
          console.log('Registrazione riuscita:', response)
          setMessage(response.data.message)
          setSuccessful(true)
          // Reindirizza alla pagina di login dopo 2 secondi
          setTimeout(() => {
            navigate('/login')
          }, 2000)
        },
        (error) => {
          console.error('Errore durante la registrazione:', error)

          if (error.response) {
            // La richiesta  stata effettuata e il server ha risposto con un codice di stato
            // al di fuori dell'intervallo 2xx
            console.error('Dati risposta:', error.response.data)
            console.error('Stato:', error.response.status)
            console.error('Headers:', error.response.headers)
          } else if (error.request) {
            // La richiesta  stata effettuata ma non  stata ricevuta alcuna risposta
            console.error('Nessuna risposta ricevuta:', error.request)
          } else {
            // Si  verificato un errore durante l'impostazione della richiesta
            console.error('Errore di configurazione:', error.message)
          }

          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString()

          console.error('Messaggio errore formattato:', resMessage)

          setMessage(resMessage)
          setSuccessful(false)
          setLoading(false)
        }
      )
      .finally(() => {
        // Ripristina la funzione fetch originale
        window.fetch = originalFetch
      })
  }

  return (
    <Container className="py-5">
      <Row className="justify-content-center">
        <Col lg={8}>
          <Card className="shadow-sm border-0">
            <Card.Body className="p-5">
              <h2 className="text-center mb-4">Crea un Account</h2>

              {message && (
                <Alert variant={successful ? 'success' : 'danger'}>
                  {message}
                </Alert>
              )}

              <Form onSubmit={handleRegister}>
                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="firstName">
                      <Form.Label>Nome</Form.Label>
                      <Form.Control
                        type="text"
                        name="firstName"
                        value={values.firstName}
                        onChange={handleChange}
                        placeholder="Inserisci il tuo nome"
                        required
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="lastName">
                      <Form.Label>Cognome</Form.Label>
                      <Form.Control
                        type="text"
                        name="lastName"
                        value={values.lastName}
                        onChange={handleChange}
                        placeholder="Inserisci il tuo cognome"
                        required
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="email">
                      <Form.Label>Email</Form.Label>
                      <Form.Control
                        type="email"
                        name="email"
                        value={values.email}
                        onChange={handleChange}
                        placeholder="Inserisci la tua email"
                        required
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="phone">
                      <Form.Label>Telefono</Form.Label>
                      <Form.Control
                        type="tel"
                        name="phone"
                        value={values.phone}
                        onChange={handleChange}
                        placeholder="Inserisci il tuo numero di telefono"
                        required
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="password">
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type="password"
                        name="password"
                        value={values.password}
                        onChange={handleChange}
                        placeholder="Inserisci una password"
                        required
                      />
                      <Form.Text className="text-muted">
                        La password deve contenere almeno 6 caratteri.
                      </Form.Text>
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="confirmPassword">
                      <Form.Label>Conferma Password</Form.Label>
                      <Form.Control
                        type="password"
                        name="confirmPassword"
                        value={values.confirmPassword}
                        onChange={handleChange}
                        placeholder="Conferma la tua password"
                        required
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="country">
                      <Form.Label>Paese</Form.Label>
                      <Form.Control
                        type="text"
                        name="country"
                        value={values.country}
                        onChange={handleChange}
                        placeholder="Inserisci il tuo paese"
                        required
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="city">
                      <Form.Label>Citt</Form.Label>
                      <Form.Control
                        type="text"
                        name="city"
                        value={values.city}
                        onChange={handleChange}
                        placeholder="Inserisci la tua citt"
                        required
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Form.Group className="mb-3" controlId="address">
                  <Form.Label>Indirizzo</Form.Label>
                  <Form.Control
                    type="text"
                    name="address"
                    value={values.address}
                    onChange={handleChange}
                    placeholder="Inserisci il tuo indirizzo completo"
                    required
                  />
                </Form.Group>

                <Form.Group className="mb-4" controlId="gender">
                  <Form.Label>Genere</Form.Label>
                  <Form.Select
                    name="gender"
                    value={values.gender}
                    onChange={handleChange}
                    required
                  >
                    <option value="">Seleziona</option>
                    <option value="Maschio">Maschio</option>
                    <option value="Femmina">Femmina</option>
                    <option value="Altro">Altro</option>
                    <option value="Preferisco non specificare">
                      Preferisco non specificare
                    </option>
                  </Form.Select>
                </Form.Group>

                <Button
                  variant="primary"
                  size="lg"
                  type="submit"
                  className="w-100 mb-4"
                  disabled={loading}
                >
                  {loading ? (
                    <>
                      <span
                        className="spinner-border spinner-border-sm me-2"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      Registrazione in corso...
                    </>
                  ) : (
                    'Registrati'
                  )}
                </Button>
              </Form>

              <div className="text-center">
                <p>
                  Hai gi un account? <Link to="/login">Accedi</Link>
                </p>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  )
}

export default RegisterPage
