import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import {
  Container,
  Row,
  Col,
  Button,
  Card,
  Form,
  Alert,
  Spinner,
} from 'react-bootstrap'
import ProductService from '../services/product.service'

const ProductDetailPage = ({ addToCart }) => {
  const { id } = useParams()
  const [product, setProduct] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [quantity, setQuantity] = useState(1)
  const [addedToCart, setAddedToCart] = useState(false)

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        setLoading(true)
        const data = await ProductService.getProductById(id)
        setProduct(data)
        setError(null)
      } catch (err) {
        console.error('Errore durante il caricamento del prodotto:', err)
        setError('Impossibile caricare il prodotto. Riprova pi tardi.')
      } finally {
        setLoading(false)
      }
    }

    fetchProduct()
  }, [id])

  const handleAddToCart = () => {
    if (product && quantity > 0) {
      addToCart(product, quantity)
      setAddedToCart(true)
      setTimeout(() => setAddedToCart(false), 3000)
    }
  }

  if (loading) {
    return (
      <Container className="py-5 text-center">
        <Spinner animation="border" role="status" variant="primary">
          <span className="visually-hidden">Caricamento...</span>
        </Spinner>
      </Container>
    )
  }

  if (error) {
    return (
      <Container className="py-5">
        <Alert variant="danger">{error}</Alert>
        <Link to="/products" className="btn btn-primary">
          Torna ai prodotti
        </Link>
      </Container>
    )
  }

  if (!product) {
    return (
      <Container className="py-5">
        <Alert variant="warning">Prodotto non trovato.</Alert>
        <Link to="/products" className="btn btn-primary">
          Torna ai prodotti
        </Link>
      </Container>
    )
  }

  return (
    <Container className="py-5">
      {addedToCart && (
        <Alert variant="success" className="mb-4">
          Prodotto aggiunto al carrello!
        </Alert>
      )}

      <Row>
        <Col md={6} className="mb-4">
          <Card className="h-100 border-0 shadow-sm">
            <div className="product-image-container">
              <img
                src={product.image_url}
                alt={product.name}
                className="card-img-top product-image"
                style={{ maxHeight: '400px', objectFit: 'contain' }}
              />
            </div>
          </Card>
        </Col>

        <Col md={6}>
          <h1 className="mb-3">{product.name}</h1>

          <div className="mb-3">
            <Link
              to={`/products/category/${product.category}`}
              className="text-decoration-none"
            >
              <span className="badge bg-secondary">{product.category}</span>
            </Link>
          </div>

          <p className="fs-4 fw-bold text-primary mb-3">
            {product.price?.toFixed(2)}
          </p>

          <div className="mb-4">
            <h5>Descrizione</h5>
            <p>{product.description}</p>
          </div>

          <div className="mb-4">
            <h5>Disponibilit</h5>
            {product.stock_quantity > 0 ? (
              <p className="text-success">
                <i className="bi bi-check-circle-fill me-2"></i>
                Disponibile ({product.stock_quantity} in magazzino)
              </p>
            ) : (
              <p className="text-danger">
                <i className="bi bi-x-circle-fill me-2"></i>
                Non disponibile
              </p>
            )}
          </div>

          {product.stock_quantity > 0 && (
            <div className="d-flex mb-4">
              <Form.Group className="me-3" style={{ width: '100px' }}>
                <Form.Label>Quantit</Form.Label>
                <Form.Control
                  type="number"
                  min="1"
                  max={product.stock_quantity}
                  value={quantity}
                  onChange={(e) => setQuantity(parseInt(e.target.value) || 1)}
                />
              </Form.Group>

              <div className="d-flex align-items-end">
                <Button
                  variant="primary"
                  onClick={handleAddToCart}
                  disabled={product.stock_quantity <= 0}
                >
                  <i className="bi bi-cart-plus me-2"></i>
                  Aggiungi al carrello
                </Button>
              </div>
            </div>
          )}

          <div className="mt-4">
            <Link to="/products" className="btn btn-outline-secondary">
              <i className="bi bi-arrow-left me-2"></i>
              Torna ai prodotti
            </Link>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

export default ProductDetailPage
