import React from 'react'
import { Link } from 'react-router-dom'

function CartPage({ cartItems, updateQuantity, removeFromCart }) {
  // Calcola il totale del carrello
  const calculateTotal = () => {
    return cartItems.reduce((acc, item) => acc + item.price * item.quantity, 0)
  }

  return (
    <div className="container my-5">
      <h2 className="mb-4">Il tuo carrello</h2>

      {cartItems.length === 0 ? (
        <div className="text-center py-5">
          <h3>Il tuo carrello  vuoto</h3>
          <p className="mb-4">
            Aggiungi alcuni prodotti al tuo carrello per procedere all'acquisto.
          </p>
          <Link to="/products" className="btn btn-primary">
            Continua lo shopping
          </Link>
        </div>
      ) : (
        <>
          <div className="table-responsive">
            <table className="table align-middle">
              <thead>
                <tr>
                  <th scope="col">Prodotto</th>
                  <th scope="col">Prezzo</th>
                  <th scope="col">Quantit</th>
                  <th scope="col">Totale</th>
                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {cartItems.map((item) => (
                  <tr key={item.id}>
                    <td>
                      <div className="d-flex align-items-center">
                        <img
                          src={
                            item.imageUrl || 'https://via.placeholder.com/100'
                          }
                          alt={item.name}
                          className="img-thumbnail me-3"
                          style={{
                            width: '75px',
                            height: '75px',
                            objectFit: 'cover',
                          }}
                        />
                        <div>
                          <h6 className="mb-0">{item.name}</h6>
                        </div>
                      </div>
                    </td>
                    <td>{item.price.toFixed(2)}</td>
                    <td>
                      <div className="input-group" style={{ width: '120px' }}>
                        <button
                          className="btn btn-outline-secondary"
                          type="button"
                          onClick={() =>
                            updateQuantity(item.id, item.quantity - 1)
                          }
                        >
                          -
                        </button>
                        <input
                          type="number"
                          className="form-control text-center"
                          value={item.quantity}
                          onChange={(e) =>
                            updateQuantity(
                              item.id,
                              parseInt(e.target.value) || 1
                            )
                          }
                          min="1"
                        />
                        <button
                          className="btn btn-outline-secondary"
                          type="button"
                          onClick={() =>
                            updateQuantity(item.id, item.quantity + 1)
                          }
                        >
                          +
                        </button>
                      </div>
                    </td>
                    <td>{(item.price * item.quantity).toFixed(2)}</td>
                    <td>
                      <button
                        className="btn btn-danger btn-sm"
                        onClick={() => removeFromCart(item.id)}
                      >
                        <i className="bi bi-trash"></i> Rimuovi
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <div className="row mt-4">
            <div className="col-md-6">
              <Link to="/products" className="btn btn-outline-primary">
                Continua lo shopping
              </Link>
            </div>
            <div className="col-md-6">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Riepilogo ordine</h5>
                  <div className="d-flex justify-content-between mb-3">
                    <span>Subtotale</span>
                    <span>{calculateTotal().toFixed(2)}</span>
                  </div>
                  <div className="d-flex justify-content-between mb-3">
                    <span>Spedizione</span>
                    <span>Calcolata al checkout</span>
                  </div>
                  <hr />
                  <div className="d-flex justify-content-between mb-3">
                    <strong>Totale</strong>
                    <strong>{calculateTotal().toFixed(2)}</strong>
                  </div>
                  <Link to="/checkout" className="btn btn-primary w-100">
                    Procedi al checkout
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  )
}

export default CartPage
