import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import AuthService from '../services/auth.service'

function ProfilePage({ currentUser }) {
  const [user, setUser] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    address: '',
    phone: '',
  })
  const [message, setMessage] = useState('')
  const [formLoading, setFormLoading] = useState(false)
  const [success, setSuccess] = useState(false)

  const navigate = useNavigate()

  useEffect(() => {
    console.log('ProfilePage: currentUser ricevuto:', currentUser)

    // Verifica se l'utente  gi autenticato
    if (!currentUser) {
      console.error('ProfilePage: Utente non autenticato')
      setError(
        'Utente non autenticato. Effettua il login per visualizzare il profilo.'
      )
      setLoading(false)
      return
    }

    // Imposta i dati iniziali dall'utente corrente
    setUser(currentUser)
    setFormData({
      name: currentUser.name || '',
      email: currentUser.email || '',
      address: currentUser.address || '',
      phone: currentUser.phone || '',
    })

    // Ottieni i dati aggiornati dall'API
    AuthService.getUserProfile()
      .then((response) => {
        console.log('ProfilePage: Dati profilo ricevuti:', response.data)
        setUser(response.data)
        setFormData({
          name: response.data.name || '',
          email: response.data.email || '',
          address: response.data.address || '',
          phone: response.data.phone || '',
        })
      })
      .catch((error) => {
        console.error('ProfilePage: Errore nel recupero del profilo:', error)
        // Non impostare error qui, usa gi i dati da currentUser come fallback
      })
      .finally(() => {
        setLoading(false)
      })
  }, [currentUser])

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }))
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    setFormLoading(true)
    setMessage('')
    setSuccess(false)

    try {
      console.log('ProfilePage: Invio aggiornamento profilo:', formData)
      const updatedUser = await AuthService.updateProfile(formData)
      console.log('ProfilePage: Profilo aggiornato con successo:', updatedUser)
      setUser({ ...user, ...updatedUser }) // Aggiorna lo stato locale con i dati aggiornati
      setSuccess(true)
      setMessage('Profilo aggiornato con successo!')
    } catch (error) {
      console.error('ProfilePage: Errore aggiornamento profilo:', error)
      setSuccess(false)
      setMessage(
        "Errore durante l'aggiornamento del profilo. Riprova pi tardi."
      )
    } finally {
      setFormLoading(false)
    }
  }

  // Se  in fase di caricamento, mostra il loader
  if (loading) {
    return (
      <div className="container mt-5 text-center">
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Caricamento...</span>
        </div>
        <p className="mt-3">Caricamento del profilo utente in corso...</p>
      </div>
    )
  }

  // Se c' un errore, mostra il messaggio
  if (error) {
    return (
      <div className="container mt-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
        <button className="btn btn-primary" onClick={() => navigate('/login')}>
          Vai al login
        </button>
      </div>
    )
  }

  // Se l'utente non  definito o manca di propriet fondamentali
  if (!user || !user.name || !user.email) {
    return (
      <div className="container mt-5">
        <div className="alert alert-warning" role="alert">
          Impossibile visualizzare il profilo. Dati utente incompleti o non
          disponibili.
        </div>
        <button className="btn btn-primary" onClick={() => navigate('/login')}>
          Vai al login
        </button>
      </div>
    )
  }

  return (
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Informazioni utente</h5>
              <p className="card-text">
                <strong>Username:</strong> {user.username || 'N/A'}
              </p>
              <p className="card-text">
                <strong>Email:</strong> {user.email || 'N/A'}
              </p>
              <p className="card-text">
                <strong>Ruoli:</strong>{' '}
                {user.roles && user.roles.length > 0
                  ? user.roles
                      .map((role) => role.replace('ROLE_', ''))
                      .join(', ')
                  : 'N/A'}
              </p>
              <button
                className="btn btn-primary"
                onClick={() => navigate('/orders')}
              >
                I miei ordini
              </button>
            </div>
          </div>
        </div>
        <div className="col-md-8">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Modifica profilo</h5>

              {message && (
                <div
                  className={`alert ${
                    success ? 'alert-success' : 'alert-danger'
                  }`}
                  role="alert"
                >
                  {message}
                </div>
              )}

              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label htmlFor="name" className="form-label">
                    Nome completo
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="email" className="form-label">
                    Email
                  </label>
                  <input
                    type="email"
                    className="form-control"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    disabled
                  />
                  <div className="form-text">
                    L'email non pu essere modificata.
                  </div>
                </div>

                <div className="mb-3">
                  <label htmlFor="address" className="form-label">
                    Indirizzo
                  </label>
                  <textarea
                    className="form-control"
                    id="address"
                    name="address"
                    value={formData.address}
                    onChange={handleChange}
                    rows="3"
                  ></textarea>
                </div>

                <div className="mb-3">
                  <label htmlFor="phone" className="form-label">
                    Telefono
                  </label>
                  <input
                    type="tel"
                    className="form-control"
                    id="phone"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                  />
                </div>

                <button
                  type="submit"
                  className="btn btn-primary"
                  disabled={formLoading}
                >
                  {formLoading ? (
                    <>
                      <span
                        className="spinner-border spinner-border-sm me-2"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      Aggiornamento...
                    </>
                  ) : (
                    'Aggiorna profilo'
                  )}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProfilePage
