import React, { useState, useEffect } from 'react'
import { Route, Routes } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-icons/font/bootstrap-icons.css'
import './styles/custom.css'

// Componenti layout
import Navbar from './components/Navbar'
import Footer from './components/Footer'

// Servizi
import AuthService from './services/auth.service'

// Pagine
import HomePage from './pages/HomePage'
import LoginPage from './pages/LoginPage'
import RegisterPage from './pages/RegisterPage'
import ProductsPage from './pages/ProductsPage'
import ProductDetailPage from './pages/ProductDetailPage'
import CartPage from './pages/CartPage'
import CheckoutPage from './pages/CheckoutPage'
import ProfilePage from './pages/ProfilePage'
import OrdersPage from './pages/OrdersPage'
import AdminDashboardPage from './pages/AdminDashboardPage'
import NotFoundPage from './pages/NotFoundPage'

function App() {
  const [currentUser, setCurrentUser] = useState(undefined)
  const [cartItems, setCartItems] = useState(() => {
    const savedCart = localStorage.getItem('cart')
    return savedCart ? JSON.parse(savedCart) : []
  })

  useEffect(() => {
    // Salva carrello nello storage locale
    localStorage.setItem('cart', JSON.stringify(cartItems))
  }, [cartItems])

  useEffect(() => {
    const user = AuthService.getCurrentUser()
    if (user) {
      setCurrentUser(user)
    }
  }, [])

  const handleLogout = () => {
    AuthService.logout()
    setCurrentUser(undefined)
    window.location.href = '/'
  }

  const addToCart = (product, quantity = 1) => {
    setCartItems((prevItems) => {
      // Cerca se il prodotto  gi nel carrello
      const existingItemIndex = prevItems.findIndex(
        (item) => item.id === product.id
      )

      if (existingItemIndex >= 0) {
        // Se il prodotto esiste, aggiorna la quantit
        const updatedItems = [...prevItems]
        updatedItems[existingItemIndex].quantity += quantity
        return updatedItems
      } else {
        // Altrimenti aggiungi il nuovo prodotto
        return [...prevItems, { ...product, quantity }]
      }
    })
  }

  const updateCartItem = (productId, quantity) => {
    setCartItems((prevItems) =>
      prevItems
        .map((item) => (item.id === productId ? { ...item, quantity } : item))
        .filter((item) => item.quantity > 0)
    )
  }

  const removeFromCart = (productId) => {
    setCartItems((prevItems) =>
      prevItems.filter((item) => item.id !== productId)
    )
  }

  const clearCart = () => {
    setCartItems([])
  }

  return (
    <div className="d-flex flex-column min-vh-100">
      <Navbar
        currentUser={currentUser}
        cartItems={cartItems}
        handleLogout={handleLogout}
      />

      <main className="flex-grow-1">
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/register" element={<RegisterPage />} />
          <Route path="/products" element={<ProductsPage />} />
          <Route
            path="/products/category/:categoryName"
            element={<ProductsPage />}
          />
          <Route
            path="/products/:id"
            element={<ProductDetailPage addToCart={addToCart} />}
          />
          <Route
            path="/cart"
            element={
              <CartPage
                cartItems={cartItems}
                updateCartItem={updateCartItem}
                removeFromCart={removeFromCart}
              />
            }
          />
          <Route
            path="/checkout"
            element={
              <CheckoutPage cartItems={cartItems} clearCart={clearCart} />
            }
          />
          <Route
            path="/profile"
            element={<ProfilePage currentUser={currentUser} />}
          />
          <Route
            path="/orders"
            element={<OrdersPage currentUser={currentUser} />}
          />
          <Route
            path="/admin"
            element={<AdminDashboardPage currentUser={currentUser} />}
          />
          <Route path="*" element={<NotFoundPage />} />
        </Routes>
      </main>

      <Footer />
    </div>
  )
}

export default App
