import React, { useState } from 'react'
import { Container, Row, Col, Form, Button, Card, Alert } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import AuthService from '../services/auth.service'

const LoginPage = () => {
  const navigate = useNavigate()
  const [values, setValues] = useState({
    email: '',
    password: '',
  })
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')

  const handleChange = (e) => {
    const { name, value } = e.target
    setValues({
      ...values,
      [name]: value,
    })
  }

  const handleLogin = (e) => {
    e.preventDefault()
    setMessage('')
    setLoading(true)

    // Validazione semplice
    if (!values.email || !values.password) {
      setMessage('Inserisci email e password')
      setLoading(false)
      return
    }

    console.log('Tentativo di login con:', { email: values.email })

    AuthService.login(values.email, values.password).then(
      () => {
        console.log('Login riuscito, reindirizzamento alla home')
        navigate('/')
        window.location.reload()
      },
      (error) => {
        console.error('Errore durante il login:', error)
        console.error('Response error:', error.response)

        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString()

        console.error('Messaggio di errore:', resMessage)

        setMessage(resMessage)
        setLoading(false)
      }
    )
  }

  return (
    <Container className="py-5">
      <Row className="justify-content-center">
        <Col md={6}>
          <Card className="shadow-sm border-0">
            <Card.Body className="p-5">
              <h2 className="text-center mb-4">Accedi</h2>

              {message && <Alert variant="danger">{message}</Alert>}

              <Form onSubmit={handleLogin}>
                <Form.Group className="mb-3" controlId="email">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    name="email"
                    value={values.email}
                    onChange={handleChange}
                    placeholder="Inserisci la tua email"
                    required
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="password">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type="password"
                    name="password"
                    value={values.password}
                    onChange={handleChange}
                    placeholder="Inserisci la tua password"
                    required
                  />
                </Form.Group>

                <div className="d-grid gap-2">
                  <Button
                    variant="primary"
                    type="submit"
                    disabled={loading}
                    className="mt-3"
                  >
                    {loading ? (
                      <>
                        <span
                          className="spinner-border spinner-border-sm me-2"
                          role="status"
                          aria-hidden="true"
                        ></span>
                        Caricamento...
                      </>
                    ) : (
                      'Accedi'
                    )}
                  </Button>
                </div>
              </Form>

              <div className="text-center mt-4">
                Non hai un account?{' '}
                <Link to="/register" className="text-primary">
                  Registrati
                </Link>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  )
}

export default LoginPage
