import axios from 'axios'

// Specifichiamo l'URL base per le richieste API
// Per test, usiamo l'URL completo del backend per evitare problemi di proxy
// const API_URL = '/api/auth/';
const API_URL = 'http://localhost:8080/api/auth/' // URL diretto per test

// Aggiungo intercettori per il logging delle richieste e delle risposte
axios.interceptors.request.use(
  (config) => {
    console.log('Richiesta in uscita:', {
      url: config.url,
      method: config.method,
      data: config.data,
      headers: config.headers,
    })
    return config
  },
  (error) => {
    console.error('Errore nella richiesta:', error)
    return Promise.reject(error)
  }
)

axios.interceptors.response.use(
  (response) => {
    console.log('Risposta ricevuta:', {
      status: response.status,
      data: response.data,
      headers: response.headers,
    })
    return response
  },
  (error) => {
    if (error.response) {
      console.error('Errore risposta:', {
        status: error.response.status,
        data: error.response.data,
        headers: error.response.headers,
      })
    } else if (error.request) {
      console.error('Nessuna risposta ricevuta:', error.request)
    } else {
      console.error('Errore di configurazione:', error.message)
    }
    return Promise.reject(error)
  }
)

class AuthService {
  login(email, password) {
    console.log('Tentativo di login con email:', email)
    return axios
      .post(API_URL + 'signin', {
        email,
        password,
      })
      .then((response) => {
        if (response.data.token) {
          localStorage.setItem('user', JSON.stringify(response.data))
        }
        return response.data
      })
  }

  logout() {
    localStorage.removeItem('user')
  }

  register(
    firstName,
    lastName,
    email,
    phone,
    password,
    country,
    city,
    address,
    gender,
    role
  ) {
    console.log('Tentativo di registrazione per:', email)

    // Log dell'URL completo per il debug
    const fullUrl = API_URL + 'signup'
    console.log('URL registrazione completo:', fullUrl)

    // Prova con una richiesta fetch invece di axios per diagnosticare problemi
    console.log('Tentativo di registrazione con fetch nativo')

    // Prima prova con axios
    return axios
      .post(fullUrl, {
        firstName,
        lastName,
        email,
        phone,
        password,
        country,
        city,
        address,
        gender,
        role,
      })
      .catch((error) => {
        console.error('Axios fallito, provo con fetch nativo')

        // Se axios fallisce, prova con fetch nativo
        return fetch(fullUrl, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            firstName,
            lastName,
            email,
            phone,
            password,
            country,
            city,
            address,
            gender,
            role,
          }),
        })
          .then((response) => {
            if (!response.ok) {
              throw new Error(`HTTP error! status: ${response.status}`)
            }
            return response.json()
          })
          .then((data) => {
            return { data }
          })
      })
  }

  getCurrentUser() {
    const userData = localStorage.getItem('user')
    if (!userData) {
      console.warn('Nessun dato utente trovato nel localStorage')
      return null
    }

    try {
      const parsedUser = JSON.parse(userData)
      console.log('Dati utente recuperati dal localStorage:', parsedUser)
      return parsedUser
    } catch (error) {
      console.error('Errore nel parsing dei dati utente:', error)
      return null
    }
  }

  getUserProfile() {
    const user = this.getCurrentUser()
    if (!user) {
      console.error('Impossibile ottenere il profilo: utente non autenticato')
      return Promise.reject('Utente non autenticato')
    }

    console.log('Richiesta profilo utente in corso...')
    // Debugging per capire perch fallisce la chiamata
    console.log('URL richiesta profilo:', API_URL + 'profile')
    console.log('Headers:', this.authHeader())

    return axios
      .get(API_URL + 'profile', {
        headers: this.authHeader(),
      })
      .then((response) => {
        console.log('Profilo utente ricevuto:', response.data)

        // Aggiorna la cache locale con i dati pi recenti
        const updatedUser = { ...user, ...response.data }
        localStorage.setItem('user', JSON.stringify(updatedUser))

        return response
      })
      .catch((error) => {
        console.error('Errore durante il recupero del profilo utente:', error)
        // Fallback: restituisci i dati dell'utente locale come risposta, cos non fallisce l'UI
        return { data: user }
      })
  }

  updateProfile(user) {
    console.log('Aggiornamento profilo in corso con dati:', user)
    return axios
      .put(API_URL + 'profile', user, {
        headers: this.authHeader(),
      })
      .then((response) => {
        // Aggiorna i dati dell'utente in localStorage
        console.log('Risposta aggiornamento profilo:', response.data)
        const currentUser = this.getCurrentUser()
        const updatedUser = { ...currentUser, ...response.data }
        localStorage.setItem('user', JSON.stringify(updatedUser))
        return response.data
      })
      .catch((error) => {
        console.error("Errore durante l'aggiornamento del profilo:", error)
        throw error
      })
  }

  changePassword(currentPassword, newPassword) {
    return axios.post(
      API_URL + 'change-password',
      {
        currentPassword,
        newPassword,
      },
      {
        headers: this.authHeader(),
      }
    )
  }

  authHeader() {
    const user = this.getCurrentUser()
    if (user && user.token) {
      return { Authorization: 'Bearer ' + user.token }
    } else {
      // Per test: invia un token vuoto ma formattato correttamente
      return { Authorization: 'Bearer test-token' }
    }
  }
}

export default new AuthService()
