import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import {
  Navbar as BootstrapNavbar,
  Nav,
  Container,
  NavDropdown,
  Badge,
} from 'react-bootstrap'

const Navbar = ({ currentUser, cartItems, handleLogout }) => {
  const totalItems = cartItems
    ? cartItems.reduce((sum, item) => sum + (item.quantity || 1), 0)
    : 0

  return (
    <BootstrapNavbar
      expand="lg"
      variant="dark"
      bg="dark"
      sticky="top"
      className="mb-4"
    >
      <Container>
        <BootstrapNavbar.Brand as={Link} to="/">
          RN BMX Shop
        </BootstrapNavbar.Brand>
        <BootstrapNavbar.Toggle aria-controls="basic-navbar-nav" />
        <BootstrapNavbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={NavLink} to="/">
              Home
            </Nav.Link>
            <Nav.Link as={NavLink} to="/products">
              Prodotti
            </Nav.Link>
            <NavDropdown title="Categorie" id="basic-nav-dropdown">
              <NavDropdown.Item
                as={NavLink}
                to="/products/category/Biciclette complete"
              >
                Biciclette complete
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item as={NavLink} to="/products/category/Componenti">
                Componenti
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/products/category/Accessori">
                Accessori
              </NavDropdown.Item>
              <NavDropdown.Item
                as={NavLink}
                to="/products/category/Abbigliamento"
              >
                Abbigliamento
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link as={Link} to="/cart" className="me-3 position-relative">
              <i className="bi bi-cart3 fs-5"></i>
              {totalItems > 0 && (
                <Badge
                  pill
                  bg="primary"
                  className="position-absolute top-0 start-100 translate-middle"
                >
                  {totalItems}
                </Badge>
              )}
            </Nav.Link>

            {currentUser ? (
              <>
                {currentUser.roles &&
                  currentUser.roles.includes('ROLE_ADMIN') && (
                    <Nav.Link as={NavLink} to="/admin">
                      Admin Panel
                    </Nav.Link>
                  )}
                <NavDropdown title={currentUser.username} id="user-dropdown">
                  <NavDropdown.Item as={NavLink} to="/profile">
                    <i className="bi bi-person me-2"></i>Profilo
                  </NavDropdown.Item>
                  <NavDropdown.Item as={NavLink} to="/orders">
                    <i className="bi bi-box me-2"></i>I miei ordini
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item onClick={handleLogout}>
                    <i className="bi bi-box-arrow-right me-2"></i>Logout
                  </NavDropdown.Item>
                </NavDropdown>
              </>
            ) : (
              <>
                <Nav.Link as={NavLink} to="/login" className="me-2">
                  Accedi
                </Nav.Link>
                <Nav.Link
                  as={NavLink}
                  to="/register"
                  className="btn btn-primary text-white"
                >
                  Registrati
                </Nav.Link>
              </>
            )}
          </Nav>
        </BootstrapNavbar.Collapse>
      </Container>
    </BootstrapNavbar>
  )
}

export default Navbar
