import React, { useState, useEffect } from 'react'
import ProductService from '../services/product.service'
import OrderService from '../services/order.service'
import UserService from '../services/user.service'

function AdminDashboardPage() {
  const [activeTab, setActiveTab] = useState('products')
  const [products, setProducts] = useState([])
  const [orders, setOrders] = useState([])
  const [users, setUsers] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)

        if (activeTab === 'products') {
          const response = await ProductService.getAllProducts()
          setProducts(response.data)
        } else if (activeTab === 'orders') {
          const response = await OrderService.getAllOrders()
          setOrders(response.data)
        } else if (activeTab === 'users') {
          const response = await UserService.getAllUsers()
          setUsers(response.data)
        }

        setLoading(false)
      } catch (err) {
        setError(`Errore durante il caricamento dei dati: ${err.message}`)
        setLoading(false)
        console.error(err)
      }
    }

    fetchData()
  }, [activeTab])

  const handleTabChange = (tab) => {
    setActiveTab(tab)
  }

  // Formatta la data
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'long', day: 'numeric' }
    return new Date(dateString).toLocaleDateString('it-IT', options)
  }

  // Ottieni il nome dello stato dell'ordine in italiano
  const getOrderStatusName = (status) => {
    switch (status) {
      case 'PENDING':
        return 'In attesa'
      case 'PROCESSING':
        return 'In elaborazione'
      case 'SHIPPED':
        return 'Spedito'
      case 'DELIVERED':
        return 'Consegnato'
      case 'CANCELED':
        return 'Annullato'
      default:
        return status
    }
  }

  if (loading) {
    return (
      <div className="container mt-5">
        <div className="d-flex justify-content-center">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Caricamento...</span>
          </div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mt-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      </div>
    )
  }

  return (
    <div className="container my-5">
      <h2 className="mb-4">Dashboard Amministratore</h2>

      <ul className="nav nav-tabs mb-4">
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 'products' ? 'active' : ''}`}
            onClick={() => handleTabChange('products')}
          >
            Prodotti
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 'orders' ? 'active' : ''}`}
            onClick={() => handleTabChange('orders')}
          >
            Ordini
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === 'users' ? 'active' : ''}`}
            onClick={() => handleTabChange('users')}
          >
            Utenti
          </button>
        </li>
      </ul>

      {/* Tab dei prodotti */}
      {activeTab === 'products' && (
        <div>
          <div className="d-flex justify-content-between align-items-center mb-3">
            <h3>Gestione Prodotti</h3>
            <button className="btn btn-primary">
              <i className="bi bi-plus-lg"></i> Nuovo Prodotto
            </button>
          </div>

          <div className="table-responsive">
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Immagine</th>
                  <th>Nome</th>
                  <th>Prezzo</th>
                  <th>Categoria</th>
                  <th>Stock</th>
                  <th>Azioni</th>
                </tr>
              </thead>
              <tbody>
                {products.map((product) => (
                  <tr key={product.id}>
                    <td>{product.id}</td>
                    <td>
                      <img
                        src={
                          product.imageUrl || 'https://via.placeholder.com/50'
                        }
                        alt={product.name}
                        style={{
                          width: '50px',
                          height: '50px',
                          objectFit: 'cover',
                        }}
                      />
                    </td>
                    <td>{product.name}</td>
                    <td>{product.price.toFixed(2)}</td>
                    <td>{product.category}</td>
                    <td>{product.stockQuantity}</td>
                    <td>
                      <button className="btn btn-sm btn-outline-primary me-2">
                        <i className="bi bi-pencil"></i>
                      </button>
                      <button className="btn btn-sm btn-outline-danger">
                        <i className="bi bi-trash"></i>
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Tab degli ordini */}
      {activeTab === 'orders' && (
        <div>
          <h3 className="mb-3">Gestione Ordini</h3>

          <div className="table-responsive">
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Data</th>
                  <th>Cliente</th>
                  <th>Totale</th>
                  <th>Stato</th>
                  <th>Azioni</th>
                </tr>
              </thead>
              <tbody>
                {orders.map((order) => (
                  <tr key={order.id}>
                    <td>{order.id}</td>
                    <td>{formatDate(order.orderDate)}</td>
                    <td>{order.user.username}</td>
                    <td>{order.totalAmount.toFixed(2)}</td>
                    <td>
                      <span className="badge bg-secondary">
                        {getOrderStatusName(order.status)}
                      </span>
                    </td>
                    <td>
                      <button className="btn btn-sm btn-outline-primary me-2">
                        <i className="bi bi-eye"></i> Dettagli
                      </button>
                      <button className="btn btn-sm btn-outline-success">
                        <i className="bi bi-check2"></i> Aggiorna Stato
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Tab degli utenti */}
      {activeTab === 'users' && (
        <div>
          <h3 className="mb-3">Gestione Utenti</h3>

          <div className="table-responsive">
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Username</th>
                  <th>Email</th>
                  <th>Ruoli</th>
                  <th>Data Registrazione</th>
                  <th>Azioni</th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr key={user.id}>
                    <td>{user.id}</td>
                    <td>{user.username}</td>
                    <td>{user.email}</td>
                    <td>
                      {user.roles.map((role) => (
                        <span key={role} className="badge bg-info me-1">
                          {role.replace('ROLE_', '')}
                        </span>
                      ))}
                    </td>
                    <td>{formatDate(user.createdAt)}</td>
                    <td>
                      <button className="btn btn-sm btn-outline-primary me-2">
                        <i className="bi bi-pencil"></i> Modifica
                      </button>
                      <button className="btn btn-sm btn-outline-danger">
                        <i className="bi bi-trash"></i> Elimina
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  )
}

export default AdminDashboardPage
