import axios from 'axios'
import authHeader from './auth-header'

const API_URL = '/api/users/'

// Ottiene tutti gli utenti (solo per admin)
const getAllUsers = () => {
  return axios.get(API_URL, { headers: authHeader() })
}

// Ottiene un utente specifico tramite ID
const getUserById = (id) => {
  return axios.get(API_URL + id, { headers: authHeader() })
}

// Aggiorna un utente
const updateUser = (id, userData) => {
  return axios.put(API_URL + id, userData, { headers: authHeader() })
}

// Elimina un utente
const deleteUser = (id) => {
  return axios.delete(API_URL + id, { headers: authHeader() })
}

// Modifica i ruoli di un utente (solo per admin)
const updateUserRoles = (id, roles) => {
  return axios.put(
    API_URL + id + '/roles',
    { roles },
    { headers: authHeader() }
  )
}

// Ottiene il profilo dell'utente corrente
const getCurrentUserProfile = () => {
  return axios.get(API_URL + 'profile', { headers: authHeader() })
}

// Aggiorna il profilo dell'utente corrente
const updateProfile = (profileData) => {
  return axios.put(API_URL + 'profile', profileData, { headers: authHeader() })
}

// Modifica la password dell'utente corrente
const changePassword = (currentPassword, newPassword) => {
  return axios.post(
    API_URL + 'change-password',
    { currentPassword, newPassword },
    { headers: authHeader() }
  )
}

export default {
  getAllUsers,
  getUserById,
  updateUser,
  deleteUser,
  updateUserRoles,
  getCurrentUserProfile,
  updateProfile,
  changePassword,
}
